.header {
  width: 100%;
  height: 500px;
  padding-top: 27px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  background-image: url("../img/PhotoFilter.jpg");
  background-size: cover;
  background-position: center;
}
.header__menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 37px;
}
.header__logo-img {
  width: 164px;
  height: 34px;
}
.header__logo {
  order: 1;
  flex-basis: 50%;
}
.header__button-container {
  flex-basis: 100%;
  text-align: right;
  order: 3;
}
.header__navigation-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  top: 5px;
  right: 22px;
}
.header__navigation-list--opened {
  visibility: visible;
  z-index: 10;
}
.header__navigation-list:last-child li {
  margin-top: 10px;
}
.header__navigation-list--closed {
  visibility: hidden;
}
.header__navigation-item {
  position: relative;
}
.header__navigation-item:active .header__navigation-sublist,
.header__navigation-item:focus .header__navigation-sublist,
.header__navigation-item:hover .header__navigation-sublist {
  display: block;
}
.header__button {
  order: 2;
  height: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__button:focus {
  background-color: #fcff19;
  border-radius: 10px;
  transition: background 0.5s;
}
.header__button:focus svg {
  fill: #000000;
}
.header__button:hover {
  background-color: #fcff19;
  border-radius: 10px;
  transition: background 0.5s;
}
.header__button:hover .header__button-svg {
  fill: #000000;
}
.header__button-svg__cross {
  fill: #ffffff;
  width: 30px;
  height: 40px;
}
.header__button-svg__cross--closed {
  display: block;
}
.header__button-svg__cross--opened {
  display: none;
}
.header__button-svg {
  width: 30px;
  height: 40px;
  fill: #ffffff;
}
.header__button-svg--opened {
  display: none;
}
.header__button-svg--closed {
  display: block;
}
.header__navigation-link {
  box-sizing: border-box;
  height: 34px;
}
.header__navigation-link--opened {
  visibility: visible;
}
.header__navigation-link--icon {
  height: 44px;
  display: flex;
  justify-content: center;
}
.header__navigation-sublist {
  display: none;
  position: absolute;
  z-index: 1;
  left: -120px;
  top: 5px;
  border: 1px solid black;
  margin: 0;
  padding: 0;
  background-color: #fcff19;
  list-style: none;
  border-radius: 10px;
}
.header__navigation-link {
  width: 100px;
  text-align: center;
}
.header__navigation-link:hover {
  border: 1px solid black;
  box-shadow: 2px 3px 0 0 #fcff19;
}
.header__navigation-link,
.header__navigation-sublink {
  display: block;
  padding: 10px;
  font-size: 12px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
}
.header__navigation-link--icon svg,
.header__navigation-sublink--icon svg {
  fill: #ffffff;
  width: 33px;
  height: 27px;
}
.header__navigation-link:hover,
.header__navigation-sublink:hover {
  background-color: #fcff19;
  border-radius: 10px;
  color: #000000;
}
.header__navigation-link--icon:hover svg,
.header__navigation-sublink--icon:hover svg {
  fill: #000000;
}
.header__navigation-sublink {
  color: #000000;
}
@media (min-width: 1100px) {
  .header {
    padding-top: 0;
  }
  .header__logo {
    margin-top: 27px;
  }
  .header__button {
    display: none;
  }
  .header__navigation-list {
    visibility: visible;
    flex-direction: row;
  }
  .header__logo {
    flex-basis: auto;
  }
  .header__button-container {
    flex-basis: auto;
  }
  .header__navigation-item {
    margin-top: 0;
  }
  .header__navigation-item:last-child li {
    margin-top: 0;
  }
  .header__navigation-link {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .header__navigation-link--icon {
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  .header__navigation-link:hover {
    border: 0;
    box-shadow: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .header__navigation-sublist {
    border-top-left-radius: 0;
    top: auto;
    border: 0;
    left: 0;
    width: 220px;
    text-align: left;
    padding: 14px;
  }
}
body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}
.visually-hidden,
.visually_hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1100px) {
  .container {
    width: 1100px;
    margin: 0 auto;
  }
}
.main {
  background-color: blue;
  width: 100%;
  height: 500px;
}
.slaider {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.slaider__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.slaider__list {
  position: relative;
  color: #ffffff;
}
.slaider__item {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
}
.slaider__item:first-child {
  transform: translateX(-100%);
}
.slaider__item:last-child {
  transform: translateX(100%);
}
.slaider__name {
  font-size: 17px;
  margin-top: 240px;
  margin-bottom: 0;
}
.slaider__link {
  padding: 16px 29px;
  background-color: #000000;
  border-radius: 10px;
  font-size: 9px;
  color: #fcff19;
  text-decoration: none;
}
.slaider__description {
  margin-left: auto;
  margin-right: auto;
  font-size: 13px;
  font-weight: 100;
  margin-top: 0;
  margin-bottom: 30px;
}
.slaider__name-yellow {
  color: #fcff19;
}
.slaider__button {
  position: absolute;
  bottom: 182px;
  width: 39px;
  height: 60px;
  padding-left: 0;
  padding-right: 0;
  background-color: transparent;
  border: none;
}
.slaider__button--right {
  right: 0;
}
.slaider__button-image {
  stroke: #ffffff;
}
.slaider__button:active .slaider__button-image {
  stroke: rgba(255, 255, 255, 0.5);
}
.slaider__button-image {
  width: 30px;
  height: 30px;
}
